.wrapper {
    height: 90px;
    align-items: center;
    // box-shadow: 0px 1px 1px rgb(0 0 0 / 15%);
    display: flex;
    justify-content: space-between;
    background-color: var(--primary);
    z-index: 1;
    padding: 0 20px;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
}
.header-content {
    height: 100%;
    width: 1279.42px;
    justify-content: space-between;
    background-color: var(--primary);
    display: flex;
    margin: 20px;
    padding: 18px;
    color: aliceblue;
}
.logo {
    height: 68px;
    width: 106px;
    align-items: center;
    position: relative;
    top: 0;
    margin-top: 8px;
    border-radius: 40%;
}
.menu {
    width: 100%;
    height: 45px;
    color: aliceblue;
    text-decoration: none;
    list-style: none;
    display: flex;
    align-items: center;

    ul {
        display: flex;
        list-style-type: none;
        width: 100%;
        justify-content: center;
        text-transform: uppercase;

        li {
            padding: 0 30px;
            a {
                text-decoration: none;
                color: white;
            }
        }
    }
}
.first-logo {
    display: flex;
    align-items: center;
    .header-login {
        display: flex;

        a {
            text-decoration: none;
            color: white;
            padding: 10px 20px;
            font-size: 26px;
        }
    }
    .header-search {
        display: flex;
        justify-content: center;
        h4 {
            color: white;
            cursor: pointer;
            font-size: 26px;
        }
        input {
            width: 190px;
            height: 26px;
        }
        .p {
            display: flex;
            padding-top: 10px;
        }
    }
}
